import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Container,Nav,Navbar,Row, Col} from 'react-bootstrap';
import { lazy, Suspense, useEffect, useState } from 'react';

import {Routes, Route, Link, useNavigate, Outlet} from 'react-router-dom';

import axios from 'axios';

import {useQuery} from 'react-query';

import data from './data.js';

import item1 from './img/item1.png';
import item2 from './img/item2.png';
import item3 from './img/item3.png';
import item4 from './img/item4.png';
import item5 from './img/item5.png';
import item6 from './img/item6.png';

//Lazy Load
const Detail = lazy( ()=> import('./pages/Detail.js'));
const Cart = lazy( () => import('./pages/Cart.js'));

// import Detail from './pages/Detail.js';
// import Loader from './pages/Loader.js';
// import Cart from './pages/Cart.js';

function App() {

  useEffect( ()=>{

    let prevData = localStorage.getItem('recentView')
    if(prevData == null)
        localStorage.setItem('recentView',JSON.stringify([]))
        
  },[])

  //detail Page 함수
  function clickFunc(i)
  {
    return(
      () => {
         navigate('/detail/'+i);        
      }      
    )
  }

  //아무튼 서버에서 데이터 받아왔음
  let [product, setProduct] = useState(data);
  let [itemImg, setItemImg] = useState([item1,item2,item3,item4,item5,item6]);

  let [moreInfo, setMoreInfo] = useState(0);
  let [loading, setLoading] = useState(0);

  //페이지 이동
  let navigate = useNavigate();

  //axios.get('https://codingapple1.github.io/userdata.json').then( (a) => { console.log(a.data)})            
  //react query 사용 ajax get 요청
  //장점 
  //1. 성공/ 실패 / 로딩중 파악 가능 (result.data / result.isLoading / result.error 등등)
  //2. 틈만 나면 자동으로 재요청을 보냄 (알아서) / refetch 라고 함 => 실시간 sns, 코인거래소 등에 적절
  //3. 요청 실패 시 자동으로 retry를 함 
  //4. state를 공유 안해도 된다
  //5. ajax 결과 캐싱기능 (ajax 결과를 5분정도 보존함)
   let result = useQuery( 'funcName', ()=>{
    return axios.get('https://codingapple1.github.io/userdata.json').then( (a) => { 
      return console.log(a.data)
    }),
    //refetch 주기 설정 가능
    {staleTime:2000} 
  });

  return (
    <div className="App">

      <Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand onClick={ ()=>{ navigate('/') } }>CLEEP</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={ ()=>{ navigate('/') } }>Home</Nav.Link>
            <Nav.Link onClick={ ()=>{ navigate('/detail/0')} }>Detail</Nav.Link>
            <Nav.Link onClick={ ()=>{ navigate('/cart/')} }>Cart</Nav.Link>
          </Nav>
          <Nav className="ms-auto"> Hi {result.data && result.data.name} 
          </Nav>
        </Container>
      </Navbar>

      <Suspense fallback={<>로오오딩중</>}>
        <Routes>
          <Route path="/" element={ 
            <>
            <div className='main-bg'>
              <button onClick={ () => { 
                let copy = [...product];
                copy.sort((a,b)=>{
                  let fa = a.title;
                  let fb = b.title;
                  if(fa < fb)
                    return -1;
                  if(fa > fb) 
                    return 1;
                  return 0;
                } );
                setProduct(copy);
                console.log(copy);
              }}>정렬</button>
              
              <button onClick={()=>{ 
                    if(moreInfo >=2)
                      alert('없어');
                    else{
                      //setLoading(1);
                      setMoreInfo(moreInfo+1);
                      axios.get('https://codingapple1.github.io/shop/data2.json')                    
                      .then((result)=>{
                        console.log(result.data);
                        let copy = [...product, ...result.data];
                        setProduct(copy);
                        //setLoading(0);
                      })
                      .catch( ()=>{
                        //setLoading(0);
                      })}
                }}>더보기</button>
            </div>
            <div className='container'>
              <div className='row'>
                {
                product.map(function(value, i){
                    return(          
                        <Card key={i} product = {product[i]} itemImg={itemImg[value.id]} clickFunc = {clickFunc(product[i].id)}/>
                        )
                  })}
              </div>
            </div>   
          </>       
          }/>        
          <Route path="/detail/:id" element={ 
          
              <Detail product = {product} itemImg={itemImg}/>       
          }/>         
          <Route path="/cart" element={<Cart></Cart>}/>
        </Routes>
      </Suspense>
    </div>
  );
}

//Component 
function Card(props){
  return(
    <div className ="col-md-4">
      <img src = {props.itemImg} width="80%"/>
      <h4>{props.product.title}</h4>
      <p>{props.product.content}</p>
      <p>KRW {props.product.price}</p>
      <button onClick={()=>{props.clickFunc()}}>구매하기</button>
    </div>
  )
}

// function About(){
//   return(
//     <>
//     <Outlet></Outlet>
//     </>
//   )
// }

export default App;
